body {
  background-color: #fefefe;
  margin: 0;
  font-family: codec, codec-thin, codec-bold,-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
  font-family: "codec-thin";
  src: url(../src/Fonts/Codec-Cold-Thin-trial.ttf) format("truetype");
}
@font-face {
  font-family: "codec";
  src: url(../src/Fonts/Codec-Cold-Regular-trial.ttf) format("truetype");
}
@font-face {
  font-family: "codec-bold";
  src: url(../src/Fonts/Codec-Cold-ExtraBold-trial.ttf) format("truetype");
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
